import React, { Component } from "react";
import {
	NavLink
} from "react-router-dom";

class Submenu extends Component {
	render() {
		return (
			<div className="content _full">
				<ul className="submenu">
					<li className="submenu-item"><NavLink replace to="/seed/wheat/winter"> </NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/wheat/spring"> </NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/soy"></NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/sunflower"></NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/barley/spring"> </NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/barley/winter"> </NavLink></li>
					<li className="submenu-item"><NavLink replace to="/seed/raps"></NavLink></li>
				</ul>
			</div>
		);
	}
}

export default Submenu;


