import React, { Component } from "react";
import Products from "./components/products";

class WheatSpring extends Component {
	constructor() {
		super();
		this.name = "wheat_winter";
		this.seeds  = [
			{ name: '', img: 'midas'},
			{ name: '', img: 'balaton'},
			{ name: '', img: 'fulvio'},
		];
	}
	render() {
		return (
			<div className="content">
				<div className="product-title">
					<h2> </h2>
					<div className="product-subtitle">
						     ,   ,   ,      .
					</div>
				</div>
				<Products name={this.name} seeds={this.seeds} />
			</div>
		);
	}
}

export default WheatSpring;


