import React, { Component } from "react";
import SkyLight from 'react-skylight';


class DetailsTable extends Component {
	render() {
		return (
			<div className="seed-details">
				{/*{this.props.details}*/}
			</div>
		);
	}
}

class Details extends Component {
	render() {
		return (
			<div className="seed-details">
				{}
				<DetailsTable />
			</div>
		);
	}
}

class Product extends Component {
	render() {
		return (
			<div className="product-item-container">
				<button className="product-item-mask" onClick={() => this.simpleDialog.show()}>
					<div className="product-item">
						<img src={require(`../../../images/seeds/${this.props.seed.img}.jpg`)} alt="" />
					</div>
					<div className="caption">{this.props.seed.name}</div>
					{/*{this.props.seed.details.subtitle}*/}
				</button>
				<SkyLight hideOnOverlayClicked ref={ref => this.simpleDialog = ref} title="Hi, I'm a simple modal">
					<div className="product-item">
						<img src={require(`../../../images/seeds/${this.props.seed.img}.jpg`)} alt="" />
						<Details name={this.props.name} details={this.props.seed.id} />
						{/*{this.props.seed.details.details_list}*/}
					</div>
				</SkyLight>
			</div>
		);
	}
}

class Products extends Component {
	render() {
		const items = [];
		this.props.seeds.forEach((product) => {
			items.push(
				<Product
					seed={product}
					name={this.props.name}
				/>
			)});

		return (
			<div className="products">

				{items}

			</div>
		);
	}
}

export default Products;


