import React, { Component } from 'react';
import {
	Route,
	NavLink,
	BrowserRouter
} from "react-router-dom";

export default class SeedHome extends Component {
	render() {
		return (
			<BrowserRouter basename="/seed">
				<div className="page-seeds">
					<div className="content _full _wheat">
						<NavLink to="/wheat/winter"> </NavLink>
						<NavLink to="/wheat/spring"> </NavLink>
					</div>
					<div className="content _full">
						<NavLink to="/soy"></NavLink>
					</div>
					<div className="content _full">
						<NavLink to="/sunflower"></NavLink>
					</div>
					<div className="content _full">
						<NavLink to="/barley/spring"> </NavLink>
						<NavLink to="/barley/winter"> </NavLink>
					</div>
					<div className="content _full">
						<NavLink to="/raps"></NavLink>
					</div>
				</div>
			</BrowserRouter>
		);
	}
};


