import React, { Component } from "react";
import Products from "./components/products";

class Raps extends Component {
	constructor() {
		super();
		this.name = "raps";
		this.seeds  = [
			{ name: '', img: 'orlando'},
			{ name: '', img: 'pedro'}
		];
	}
	render() {
		return (
			<div className="content">
				<div className="product-title">
					<h2></h2>
					<div className="product-subtitle">
						     ,   ,   ,      .
					</div>
				</div>
				<Products name={this.name} seeds={this.seeds} />
			</div>
		);
	}
}

export default Raps;


